<template>
  <div>
    <highcharts
      :options="optionConfig"
      :callback="myCallback"
      class="charts-content"
    ></highcharts>
    <p>近三年收入</p>
    <ul>
      <li>
        <span
          ><h1>今年总收入</h1>
          ￥：{{ this.allIncome.now }}</span
        >
      </li>
      <li>
        <span
          ><h1>去年总收入</h1>
          ￥：{{ this.allIncome.last }}</span
        >
      </li>
      <li>
        <span
          ><h1>前年总收入</h1>
          ￥：{{ this.allIncome.before }}</span
        >
      </li>
      <li>
        <h1 style="line-height: 0px">
          三年对比<span style="font-size: 10px; font-weight: 400; color: #999"
            >（单位:K）</span
          >
        </h1>
        <div class="echart" id="mychart" :style="myChartStyle"></div>
      </li>
    </ul>
  </div>
</template>

<script>
import * as echarts from "echarts";
// 指定图表的配置项和数据
const chartOptions = {
  title: {
    text: "2022年收入统计图",
  },
  subtitle: {
    text: "数据来源：MySQL",
  },
  xAxis: {
    // title: {
    //   text: "月份",
    // },
    categories: [
      "一月",
      "二月",
      "三月",
      "四月",
      "五月",
      "六月",
      "七月",
      "八月",
      "九月",
      "十月",
      "十一月",
      "十二月",
    ],
  },
  yAxis: {
    title: {
      text: "收入详情",
    },
  },
  legend: {
    layout: "vertical",
    align: "right",
    verticalAlign: "middle",
  },
  plotOptions: {
    series: {
      label: {
        connectorAllowed: false,
      },
    },
  },
  series: [
    {
      name: "收入曲线",
      data: [
        43934, 52503, 57177, 69658, 97031, 119931, 137133, 154175, 97031,
        119931, 137133, 154175,
      ],
    },
  ],
  responsive: {
    rules: [
      {
        condition: {
          maxWidth: 500,
        },
        chartOptions: {
          legend: {
            layout: "horizontal",
            align: "center",
            verticalAlign: "bottom",
          },
        },
      },
    ],
  },
};

export default {
  name: "Finance",
  data() {
    return {
      newYear: "",
      optionConfig: {},
      allIncome: {
        now: 10000,
        last: 20000,
        before: 30000,
      },
      xData: ["今年", "去年", "前年"], //横坐标
      yData: [], //数据
      myChartStyle: {
        float: "right",
        width: "105%",
        height: "190px",
        margin: "-40px -15px 0 15px",
      }, //图表样式
    };
  },
  created() {
    this.optionConfig = chartOptions;
  },
  updated() {},
  mounted() {
    this.initEcharts();
  },
  watch: {},
  methods: {
    myCallback() {
      this.$axios.get("/admin/getIncome").then((res) => {
        this.optionConfig.series[0].data = res.data.data.income;
        this.newYear = res.data.data.year;
        this.allIncome.now = res.data.data.incomeEveryYear[0];
        this.allIncome.last = res.data.data.incomeEveryYear[1];
        this.allIncome.before = res.data.data.incomeEveryYear[2];
        this.yData = res.data.data.incomeEveryYear;
        this.initEcharts();
        this.optionConfig.title.text = `${this.newYear}年收入统计图`;
      });
    },
    initEcharts() {
      // 基本柱状图
      const option = {
        xAxis: {
          data: this.xData,
        },
        yAxis: {
          title: {
            text: "单位:K",
          },
        },
        series: [
          {
            type: "bar", //形状为柱状图
            data: this.yData,
          },
        ],
      };
      const myChart = echarts.init(document.getElementById("mychart"));
      myChart.setOption(option);
      //随着屏幕大小调节图表
      window.addEventListener("resize", () => {
        myChart.resize();
      });
    },
  },
};
</script>

<style lang="less" scoped>
.charts-content {
  width: 100%;
  height: 275px;
  padding: 0;
  margin: 0;
}
/deep/.highcharts-title {
  font-size: 20px;
  font-weight: 500;
}
ul {
  display: flex;
  justify-content: space-between;
  padding: 0;
  height: 130px;
  margin: 20px 50px;
  list-style: none;
  li {
    width: 20%;
    height: 130px;
    border-radius: 10px;
    box-shadow: 1px 1px 10px #ccc;
    background-image: linear-gradient(to bottom right, #fbd16d, #fcb679);
    h1 {
      padding-top: 30px;
      display: flex;
      justify-content: center;
      font-size: 20px;
      font-weight: 550;
    }
    span {
      line-height: 0;
    }
  }
}
p {
  font-size: 20px;
  font-weight: 550;
  margin: 0 50px;
  line-height: 50px;
  height: 50px;
}
canvas {
  width: 100%;
  height: 150px;
  margin-top: -50px !important;
}
</style>